:root {
  font-family: 'Quicksand', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
  font-weight: 400;

  color-scheme: light dark;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  margin: 0;
  background: linear-gradient(135deg, #1a1614 0%, #2a1f1a 50%, #1a1614 100%);
  background-attachment: fixed;
  color: #f7f1ed;
  min-height: 100vh;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image:
    radial-gradient(circle at 20% 30%, rgba(166, 124, 82, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(166, 124, 82, 0.06) 0%, transparent 50%),
    radial-gradient(circle at 50% 50%, rgba(209, 159, 110, 0.04) 0%, transparent 70%);
  pointer-events: none;
  z-index: 0;
}

* {
  box-sizing: border-box;
}

.error-content {
  text-align: center;
  margin-top: 5rem;
  padding: 2rem;
}

.error-content h1 {
  font-size: 3rem;
  color: #d19f6e;
}

.error-content p {
  font-size: 1.5rem;
  opacity: 0.8;
}

.error-content .button {
  display: inline-block;
  margin-top: 1.5rem;
  padding: 0.75rem 1.5rem;
  background: #a67c52;
  color: #1a0c03;
  text-decoration: none;
  border-radius: 6px;
  font-weight: bold;
}

.error-content .button:hover {
  background: #d19f6e;
}
